.search-widget {
	--search-widget-bg: var(--black);
	position: fixed;
	top: 0;
	left: 0;
	display: grid;
	grid-template-rows: 40px 1fr;
	max-width: 360px;
	width: 100%;
	height: 100vh;
	z-index: 9;
	transform: translateX(-100%);
	transition: transform 0.2s ease-in-out;
	&--active {
		transform: translateX(0);
	}
	&__action-area {
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 10px;
		padding: 0 10px;
		height: 40px;
		background: var(--search-widget-bg);
	}
	&__result-area {
		display: block;
		padding: 10px 10px 60px;
		width: 100%;
		max-height: calc(100vh - 40px);
		background: var(--white-dark);
		overflow-y: auto;
		visibility: visible;
		&:empty {
			display: none;
			visibility: hidden;
		}
	}
	.search-area {
		max-width: 100%;
	}
	@media screen and (min-width: 1800px) {
		grid-template-rows: 60px 1fr;
		max-width: 440px;
		&__action-area {
			height: 60px;
		}
	}
}

.search-result {
	&__item {
		display: flex;
		margin-bottom: 10px;
	}
	&__img-link {
		display: block;
		width: 105px;
		min-width: 105px;
	}
	&__content-box {
		flex: 1;
	}
	&__content-top {
		display: block;
		padding: 10px;
		background: var(--white);
		&:hover {
			background: var(--decor-color);
			.search-result {
				&__title,
				&__sub-title {
					color: var(--white);
				}
			}
		}
	}
	&__content-bottom {
		display: block;
		padding: 0 10px;
	}
	&__row {
		display: flex;
		justify-content: flex-end;
	}
	&__row-item {
		flex-basis: 50%;
		&--center {
			display: flex;
			justify-content: center;
		}
	}
	&__title {
		display: block;
		max-width: 94px;
		width: 100%;
		font-size: 14px;
		font-weight: 700;
		color: var(--gray-dark);
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	&__sub-title {
		display: block;
		max-width: 94px;
		width: 100%;
		font-size: 10px;
		color: var(--gray);
		text-transform: uppercase;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	&__btn {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		padding: 0 10px;
		height: 20px;
		font-size: 10px;
		font-weight: 700;
		letter-spacing: 0.1em;
		color: var(--white);
		text-transform: uppercase;
		background: var(--gray-dark);
		border-radius: 0 0 4px 4px;
	}
	@media screen and (min-width: 1800px) {
		&__img-link {
			display: block;
			width: 145px;
			min-width: 145px;
		}
		&__title {
			max-width: 119px;
			font-size: 16px;
		}
		&__sub-title {
			max-width: 119px;
			font-size: 12px;
		}
		&__btn {
			padding: 0 15px;
			height: 30px;
			font-size: 12px;
		}
	}
}
