.user-menu {
	--action-area-bg: var(--black);
	--nav-bg: var(--white-dark);
	--link-bg: transparent;
	--link-color: var(--gray-dark);
	position: fixed;
	top: 0;
	right: 0;
	display: grid;
	grid-template-rows: 40px 1fr 60px;
	max-width: 360px;
	width: 100vw;
	height: 100vh;
	z-index: 9;
	pointer-events: none;
	transform: translateX(100%);
	transition: transform 0.3s ease-in-out;
	&--active {
		pointer-events: all;
		transform: translateX(0);
	}
	&__action-area {
		display: flex;
		align-items: center;
		gap: 10px;
		padding: 0 10px;
		background: var(--action-area-bg);
		.small-btn--black {
			--btn-font-color: var(--white);
		}
	}
	&__deposit-link {
		flex: 1;
	}
	&__nav {
		display: block;
		padding: 15px 20px 30px;
		background: var(--nav-bg);
		overflow-y: auto;
		@media screen and (min-width: 360px) {
			padding: 20px 20px 30px;
		}
	}
	&__row {
		display: flex;
		gap: 10px;
		margin-bottom: 10px;
		@media screen and (min-width: 360px) {
			gap: 20px;
		}
	}
	&__list {
		flex: 1;
	}
	&__item {
		position: relative;
		margin-bottom: 10px;
		&--sub-menu {
			&::after {
				content: "";
				display: block;
				position: absolute;
				top: 20px;
				right: 16px;
				width: 0;
				height: 0;
				border-style: solid;
				border-width: 4px 4px 0 4px;
				border-color: var(--font-color) transparent transparent
					transparent;
				pointer-events: none;
				transition: transform 0.2s ease-in-out;
			}
		}
		&--active {
			&::after {
				transform: rotate(180deg);
			}
		}
	}
	&__link {
		position: relative;
		display: flex;
		align-items: center;
		padding: 0 10px 0 30px;
		width: 100%;
		height: 30px;
		font-size: 10px;
		color: var(--link-color);
		background: var(--link-bg);
		border-radius: 4px;
		&:hover {
			--link-color: var(--white);
			--link-bg: var(--gray);
		}
		&--current {
			--link-color: var(--white);
			--link-bg: var(--gray-dark);
		}
	}
	&__link-info {
		text-transform: uppercase;
	}
	&__link-alert {
		position: absolute;
		top: 2px;
		left: 10px;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 16px;
		height: 16px;
		font-size: 10px;
		font-weight: 700;
		color: var(--white);
		border-radius: 50%;
		background: var(--red);
		z-index: 2;
		&:empty {
			display: none;
			visibility: hidden;
		}
	}
	&__footer {
		display: flex;
		justify-content: center;
		align-items: flex-end;
		padding: 0 10px;
		width: 100%;
		height: 60px;
		background: var(--action-area-bg);
	}
	.user {
		&__short-info {
			height: 42px;
		}
		&__value-item {
			font-size: 14px;
		}
		&__img-box {
			width: 42px;
			min-width: 42px;
			height: 42px;
		}
		&__img {
			width: 40px;
			min-width: 40px;
			height: 40px;
		}
	}
	@media screen and (min-width: 1800px) {
		grid-template-rows: 60px 1fr 60px;
		&__link {
			height: 40px;
			font-size: 14px;
			font-weight: 700;
		}
		.user {
			&__type-side {
				font-size: 12px;
			}
			&__value-item {
				font-size: 16px;
			}
		}
		.hamburger {
			width: 40px;
			min-width: 40px;
			height: 40px;
		}
	}
}
