:root {
	// global colors
	--white: #fff;
	--white-dark: #e4e6eb;

	--black: #1d2127;
	--black-dark: #171a1e;

	--gray: #95a1b6;
	--gray-dark: #262b33;
	
	--yellow: #ffcc00;
	--red: #ff0066;
	--green: #00ff99;
	--blue: #0066ff;

	// work colors
	--bg-color: var(--black-dark);
	--font-color: var(--gray-dark);
	--decor-color: var(--blue);
	--error-color: var(--red);

	// work variables
	--header-height: 70px;
	@media screen and (min-width: 1800px) {
		--header-height: 100px;
	}
}