.e-sports {
	&__slider {
		display: none;
		visibility: hidden;
		margin: 0 -5px;
		&.slick-initialized {
			display: block;
			visibility: visible;
		}
		.slick-list {
			mask-image: linear-gradient(
				90deg,
				#fff,
				#fff 2%,
				#fff 98%,
				hsla(0deg, 0%, 100%, 0)
			);
		}
	}
	&__item {
		display: block;
		padding: 0 5px;
		width: 170px;
	}
	&__img-box {
		position: relative;
		display: block;
		width: 100%;
		height: 0;
		padding-bottom: 150%;
		overflow: hidden;
		&:hover {
			.e-sports__img {
				transform: scale(1.1);
			}
		}
	}
	&__img {
		position: absolute;
		top: 0;
		left: 0;
		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover;
		transition: transform 0.6s ease-in-out;
	}
	&__info-box {
		display: block;
		padding: 10px;
		width: 100%;
		height: 120px;
		font-size: 12px;
		color: var(--font-color);
		text-align: center;
		background: var(--white-dark);
	}
	// &__catagory {
	// }
	&__event {
		display: block;
		margin-bottom: 10px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	&__time {
		position: relative;
		display: block;
		margin-top: 10px;
		font-weight: 700;
		text-transform: uppercase;
		&--live {
			&::before {
				content: "";
				display: block;
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-320%, -50%);
				width: 8px;
				height: 8px;
				border-radius: 50%;
				background: var(--red);
			}
		}
	}
	&__date {
		font-size: 10px;
		line-height: 0.8;
		&:empty {
			display: none;
			visibility: hidden;
		}
	}
	@media screen and (min-width: 430px) {
		&__item {
			width: 210px;
		}
	}
	@media screen and (min-width: 1800px) {
		&__item {
			width: 316px;
			padding: 0 8px;
		}
		&__info-box {
			padding-top: 20px;
			height: 160px;
			font-size: 14px;
		}
		&__time {
			margin-top: 15px;
			font-size: 16px;
		}
		&__date {
			font-size: 12px;
		}
	}
	@media screen and (min-width: 1920px) {
		&__slider {
			.slick-list {
				mask-image: none;
			}
		}
	}
}
