*,
*:before,
*:after {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: "Roboto Condensed", sans-serif;
}

/* width / height */
::-webkit-scrollbar {
	width: 4px;
	height: 4px;
	@media screen and (min-width: 1200px) {
		width: 16px;
		height: 16px;
	}
}

/* Track */
::-webkit-scrollbar-track {
	background: var(--gray);
	// border-radius: 3px;
	@media screen and (min-width: 1200px) {
		border-radius: 8px;
	}
}

/* Handle */
::-webkit-scrollbar-thumb {
	background: var(--decor-color);
	// border-radius: 3px;
	@media screen and (min-width: 1200px) {
		border-radius: 8px;
	}
}
body {
	position: relative;
	padding-top: var(--header-height);
	font-size: 12px;
	font-weight: 400;
	color: var(--font-color);
	background: var(--bg-color);
	overflow-x: hidden;
	&.on-line {
		.registration {
			display: none;
			visibility: hidden;
		}
		.login {
			display: block;
			visibility: visible;
		}
		.action-area {
			display: none;
			visibility: hidden;
		}
	}
	.registration {
		display: block;
		visibility: visible;
	}
	.login {
		display: none;
		visibility: hidden;
	}
	@media screen and (min-width: 1600px) {
		font-size: 14px;
	}
}

.dn {
	display: none !important;
	visibility: hidden !important;
}

.decor-color {
	color: var(--decor-color);
}
.decor-bg {
	background: var(--decor-color);
}

h1,
.h1 {
	font-size: 24px;
	@media screen and (min-width: 1800px) {
		font-size: 30px;
	}
}
h2,
.h2 {
	font-size: 14px;
	font-weight: 700;
	color: inherit;
	text-transform: uppercase;
	@media screen and (min-width: 1800px) {
		font-size: 18px;
	}
}
h3,
.h3 {
	font-size: 14px;
	font-weight: 700;
	@media screen and (min-width: 1800px) {
		font-size: 16px;
	}
}
h4,
.h4 {
	font-size: 12px;
}

.ttu {
	text-transform: uppercase;
}
.small {
	font-size: 0.86em;
}

b,
strong,
.bold {
	font-weight: 700;
}
hr {
	display: block;
	width: 100%;
	height: 1px;
	background: var(--gray-dark);
	border: none;
}
.overlay {
	--overlay-bg: var(--gray-dark);
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background: var(--overlay-bg);
	opacity: 0;
	z-index: 9;
	transform: translateY(-40%);
	transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
	pointer-events: none;
	&--active {
		pointer-events: all;
		opacity: 0.8;
		transform: translateY(0);
	}
}

.wrapper {
	display: block;
	margin: 0 auto;
	padding: 0 10px;
	max-width: 1920px;
	width: 100%;
}

.row {
	display: flex;
	width: 100%;
	&--between {
		justify-content: space-between;
	}
}

main {
	min-height: 100vh;
}

// .slick-slider {
// }
.slick-prev,
.slick-next {
	--arrow-color: var(--white);
	--triangle-color: var(--black);
	display: flex;
	justify-content: center;
	align-items: center;
	width: 30px;
	height: 30px;
	background: var(--arrow-color);
	border-radius: 50%;
	opacity: 0.8;
	z-index: 3;
	&::before {
		content: "";
		display: block;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		min-width: 30px;
		height: 30px;
		font-size: 0;
		background: url(../img/icons/icons.svg#ico_arrow) center center / 20px
			20px no-repeat;
		opacity: 1;
	}
	&:focus,
	&:hover {
		background: var(--arrow-color);
		opacity: 1;
	}
}
.slick-prev {
	left: 0px;
	&::before {
		transform: translate(-50%, -50%) scale(-1);
	}
}
.slick-next {
	right: 0;
}
@media screen and (min-width: 1920px) {
	.slick-prev,
	.slick-next {
		width: 40px;
		height: 40px;
		&::before {
			background: url(../img/icons/icons.svg#ico_arrow) center center / 30px
				30px no-repeat;
		}
	}
	.slick-next {
		right: 2px;
	}
}

