.heading {
	--heading-color: var(--white);
	--heading-bg: var(--gray-dark);
	display: flex;
	justify-content: flex-start;
	align-items: center;
	// gap: 10px;
	margin-bottom: 15px;
	&--center {
		justify-content: center;
	}
	&--between {
		justify-content: space-between;
	}
	&__line {
		flex: 1;
		display: block;
		width: 100%;
		height: 1px;
		background: var(--heading-bg);
	}
	&__content {
		& * {
			margin-bottom: 0;
			white-space: nowrap;
		}
	}
	&__link {
		position: relative;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		padding-right: 16px;
		font-size: 14px;
		font-weight: 700;
		color: var(--heading-color);
		text-transform: uppercase;
		white-space: nowrap;
		&::after {
			content: "";
			position: absolute;
			top: 49%;
			right: 0;
			transform: translateY(-50%);
			display: block;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 6px 0 6px 6px;
			border-color: transparent transparent transparent var(--decor-color);
		}
		&:hover {
			--heading-color: var(--decor-color);
		}
	}
	@media screen and (min-width: 1800px) {
		&__link {
			font-size: 18px;
		}
	}
}
