.shape {
	--shape-bg: var(--white);
	--shape-border: var(--white-dark);
	display: inline-block;
	padding: 7px;
	width: auto;
	&--img {
		padding-top: 65px;
	}
	&--green {
		--shape-border: #41ae58;
	}
	&--yellow {
		--shape-border: var(--yellow);
	}
	&__close {
		position: absolute;
		top: -22px;
		right: -22px;
		z-index: 2;
	}
	&__body {
		position: relative;
		display: inline-flex;
		padding: 15px;
		width: auto;
		height: auto;
		background: var(--shape-bg);
		border: 10px solid var(--shape-border);
		border-radius: 20px;
		box-shadow: 0px 0px 0px 7px var(--shape-bg);
	}
}

.tab {
	display: block;
	width: 100%;
	&__menu {
		display: flex;
		align-items: center;
		gap: 5px;
		margin: 0 auto 15px;
		padding: 0 10px;
		max-width: 1920px;
		width: 100%;
		&--center {
			justify-content: center;
			flex-wrap: wrap;
			// gap: 10px 20px;
			.tab {
				&__menu-item {
					flex-grow: 1;
				}
				&__menu-btn {
					width: 100%;
				}
			}
		}
		&--right {
			justify-content: flex-end;
		}
		&--payment {
			justify-content: center;
			flex-wrap: wrap;
			margin-bottom: 0;
			padding: 0;
			.tab {
				&__menu-item {
					flex-grow: 1;
				}
				&__menu-btn {
					width: 100%;
				}
			}
		}
	}
	&__menu-item {
		cursor: pointer;
		&:hover {
			.h2 {
				color: var(--gray);
			}
		}
		&.tab__menu-item--active {
			.tab {
				&__step {
					color: var(--white);
					background: var(--gray-dark);
					.tab__icon {
						fill: var(--white);
					}
				}
				&__menu-btn {
					--btn-font-color: var(--white-dark);
					--btn-bg-color: var(--gray-dark);
				}
			}
			.h2 {
				color: var(--white);
			}
		}
		.h2 {
			color: var(--decor-color);
			padding-right: 15px;
		}
	}
	&__menu-btn {
		--btn-font-color: var(--font-color);
		--btn-bg-color: var(--white-dark);
		--btn-shadow-top: var(--decor-color);
		--btn-shadow-bottom: var(--blue);
		position: relative;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		padding: 0 5px;
		width: auto;
		height: 40px;
		font-size: 10px;
		// font-weight: 700;
		color: var(--btn-font-color);
		background: var(--btn-bg-color);
		cursor: pointer;
		&:hover {
			--btn-font-color: var(--white-dark);
			--btn-bg-color: var(--gray);
		}
	}
	&__step {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 30px;
		min-width: 30px;
		height: 30px;
		font-size: 14px;
		font-weight: 700;
		color: var(--gray);
		border-radius: 50%;
		background: var(--white);
		cursor: pointer;
	}
	&__img {
		display: block;
		width: 20px;
		height: 20px;
	}
	&__icon {
		fill: var(--gray);
	}
	&__menu-decor {
		flex: 1;
		display: block;
		height: 10px;
		border-bottom: 10px dotted var(--white-dark);
		pointer-events: none;
	}
	@media screen and (min-width: 360px) {
		&__menu-btn {
			font-size: 12px;
		}
	}
	@media screen and (min-width: 760px) {
		&__menu {
			&--center {
				gap: 10px 20px;
				.tab {
					&__menu-item {
						flex-grow: unset;
					}
					&__menu-btn {
						width: auto;
					}
				}
			}
			&--payment {
				justify-content: flex-start;
				.tab {
					&__menu-item {
						flex-grow: unset;
					}
					&__menu-btn {
						width: auto;
					}
				}
			}
		}
		&__menu-btn {
			padding: 0 15px;
			min-width: 160px;
			// height: 60px;
			font-size: 14px;
			// &:hover {
			// }
		}
	}
	@media screen and (min-width: 900px) {
		&__menu-btn {
			min-width: 200px;
		}
	}
	@media screen and (min-width: 1800px) {
		&__step {
			width: 40px;
			min-width: 40px;
			height: 40px;
			font-size: 16px;
		}
	}
}

.table-container {
	display: block;
	width: 100%;
	overflow-x: auto;
}
.table {
	--table-head-color: var(--gray);
	--table-color: var(--font-color);
	--table-bg: var(--white);
	border-collapse: collapse;
	margin-bottom: 20px;
	width: 100%;
	thead {
		tr {
			td,
			th {
				color: var(--table-head-color);
				text-transform: uppercase;
				background: var(--table-bg);
			}
		}
	}
	tr {
		td,
		th {
			padding: 5px 10px;
			height: 30px;
			white-space: nowrap;
			color: var(--table-color);
			border-bottom: 1px solid var(--black);
			&.decor-bg {
				background: var(--white);
			}
		}
	}
	a {
		color: var(--decor-color);
		text-decoration: underline;
		&:hover {
			text-decoration: none;
		}
	}
	&__sort {
		position: relative;
		display: inline-block;
		padding-right: 15px;
		cursor: pointer;
		text-transform: lowercase;
		&::first-letter {
			text-transform: uppercase;
		}
		&::before,
		&::after {
			content: "";
			display: block;
			position: absolute;
			right: 0;
			top: 50%;
			width: 0;
			height: 0;
			border-style: solid;
		}
		&::before {
			border-width: 0 4px 4px 4px;
			border-color: transparent transparent var(--table-head-color)
				transparent;
			-webkit-transform: translateY(-125%);
			-ms-transform: translateY(-125%);
			transform: translateY(-125%);
		}
		&::after {
			border-width: 4px 4px 0 4px;
			border-color: var(--table-head-color) transparent transparent
				transparent;
			-webkit-transform: translateY(25%);
			-ms-transform: translateY(25%);
			transform: translateY(25%);
		}
	}
	// &__ok {
	// 	color: #41ae58;
	// }
	&__cancel {
		opacity: 0.6;
		color: var(--gray);
	}
	&__reject {
		color: var(--red);
	}
	&__img-box {
		display: block;
		cursor: pointer;
	}
	&__img {
		display: block;
		width: 20px;
		height: 20px;
	}
	@media screen and (min-width: 1800px) {
		tr {
			td,
			th {
				height: 40px;
			}
		}
	}
}

.iframe-box {
	position: relative;
	display: block;
	min-width: 100%;
	min-height: 100vh;
	iframe {
		min-height: 100vh;
	}
}

ul.dot-list {
	padding-left: 10px;
	li {
		margin-bottom: 0;
		&::before {
			content: "";
			display: inline-block;
			margin-right: 5px;
			margin-bottom: 1px;
			width: 5px;
			height: 5px;
			background: var(--decor-color);
			border-radius: 50%;
		}
	}
}
