.testimonials {
	&__slider {
		margin: 0 -5px;
		display: none;
		visibility: hidden;
		&.slick-initialized {
			display: block;
			visibility: visible;
		}
	}
	&__item {
		display: inline-block;
		padding: 0 5px;
		width: 310px;
		overflow: hidden;
	}
	&__info-box {
		display: block;
		padding: 15px;
		width: 100%;
		height: 70px;
		font-size: 12px;
		color: var(--gray);
		text-align: center;
		background: var(--black-dark);
		border: 2px solid var(--gray-dark);
	}
	&__info {
		position: relative;
		display: -webkit-box;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;
		overflow: hidden;
		height: 42px;
		font-size: 12px;
		&::before {
			content: "";
		}
		&::after {
			content: "";
		}
	}
	&__author-box {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 40px;
		font-size: 10px;
		color: var(--gray);
		background: var(--black);
	}
	&__img {
		display: block;
		width: auto;
		max-height: 30px;
		height: auto;
		filter: grayscale(1);
	}
	&__author-name {
		font-size: 14px;
		font-weight: 700;
		text-transform: uppercase;
	}
	&__author-country {
		display: inline-block;
		line-height: 0.8em;
		&::first-letter {
			text-transform: uppercase;
		}
	}
	@media screen and (min-width: 330px) {
		&__item {
			width: 320px;
		}
	}
	@media screen and (min-width: 350px) {
		&__slider {
			.slick-list {
				mask-image: linear-gradient(
					90deg,
					#fff,
					#fff 2%,
					#fff 97%,
					hsla(0deg, 0%, 100%, 0)
				);
				// padding-right: calc(100vw - 340px);
			}
		}
	}
	@media screen and (min-width: 1800px) {
		&__item {
			padding: 0px 7px;
			width: 474px;
		}
		&__info-box {
			height: 80px;
		}
		&__info {
			height: 53px;
			font-size: 14px;
		}
		&__author-box {
			height: 50px;
			font-size: 12px;
		}
		&__author-name {
			font-size: 16px;
		}
	}
	@media screen and (min-width: 1900px) {
		&__slider {
			.slick-list {
				mask-image: none;
				// padding-right: 0;
			}
		}
	}
}
