// 4 Game Item
.game-item {
	position: relative;
	display: inline-block;
	max-width: 300px;
	width: 100%;
	user-select: none;
	&__widget-trigger {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 4;
	}
	&__box {
		position: relative;
		display: block;
		width: 100%;
		z-index: 1;
	}
	&__label {
		position: absolute;
		bottom: 0;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		padding: 0 5px;
		width: auto;
		height: 14px;
		font-size: 10px;
		font-weight: 700;
		letter-spacing: 0.1em;
		text-transform: uppercase;
		background: var(--gray-dark);
		border-radius: 4px;
		z-index: 4;
		pointer-events: none;
		&--new {
			right: 0;
			transform: translate(4px, 4px);
			color: var(--green);
		}
		&--jackpot {
			left: 50%;
			transform: translate(-50%, 4px);
			color: var(--yellow);
		}
		&--hot {
			left: 0;
			transform: translate(-4px, 4px);
			color: var(--red);
		}
		&:empty {
			display: none;
			visibility: hidden;
		}
	}
	// &__label-img {
	// 	display: block;
	// 	width: 40px;
	// 	height: 40px;
	// }
	// &__label-info {
	// 	display: inline-flex;
	// 	justify-content: center;
	// 	align-items: center;
		
	// }
	&__img-box {
		position: relative;
		width: 100%;
		background: var(--gray-dark);
		&:after {
			content: attr(data-title);
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			display: flex;
			justify-content: center;
			align-items: center;
			padding: 10px;
			color: var(--gray);
			font-size: 16px;
			font-weight: 700;
			text-align: center;
			z-index: 1;
			pointer-events: none;
		}
	}
	&__img-container {
		position: relative;
		display: block;
		width: 100%;
		height: 0;
		padding-bottom: 66%;
	}
	&__game-img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 2;
	}
	&__btn-box {
		position: absolute;
		top: 0;
		left: 0;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 100%;
		background: rgba(33, 33, 33, 0.8);
		z-index: 3;
		opacity: 0;
		pointer-events: none;
		transition: opacity 0.3s ease-in-out;
	}
	&__btn-row {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 10px;
		width: 100%;
	}
	&__btn {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		display: flex;
		justify-content: center;
		align-items: center;
		width: 40px;
		height: 30px;
		font-size: 10px;
		font-weight: 700;
		letter-spacing: 0.1em;
		text-transform: uppercase;
		color: var(--gray);
		background: var(--gray-dark);
		&--left {
			left: 0;
			border-radius: 0 4px 4px 0;
		}
		&--right {
			right: 0;
			border-radius:  4px 0 0 4px;
		}
		&:hover,
		&--active {
			color: var(--white);
			background: var(--decor-color);
			.small-btn-st0 {
				fill: var(--white);
			}
		}
	}
	&__jackpot {
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		margin-top: -10px;
		z-index: 4;
	}
	// &__jackpot-img-box {
	// 	display: flex;
	// 	justify-content: center;
	// 	align-items: center;
	// 	width: 40px;
	// 	height: 24px;
	// }
	// &__jackpot-img {
	// 	display: block;
	// 	width: 40px;
	// 	height: 40px;
	// }
	&__jackpot-info {
		--jackpot-font-color: var(--font-color);
		--jackpot-bg-color: var(--yellow);
		display: inline-flex;
		justify-content: center;
		align-items: center;
		padding: 0 7px;
		width: auto;
		min-width: 130px;
		height: 20px;
		font-size: 12px;
		font-weight: 700;
		letter-spacing: 0.05em;
		color: var(--jackpot-font-color);
		background: var(--jackpot-bg-color);
		border-radius: 4px;
	}
	&__title {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 30px;
		font-size: 10px;
		font-weight: 700;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		color: var(--white);
	}
	.btn {
		letter-spacing: 0.04em;
	}
	@media screen and (min-width: 1200px) {
		&:hover {
			.game-item {
				&__btn-box {
					opacity: 1;
					pointer-events: all;
				}
			}
		}
		&__widget-trigger {
			display: none;
			visibility: hidden;
		}
		&__img-box {
			&:after {
				font-size: 24px;
			}
		}
	}
	@media screen and (min-width: 1800px) {
		&__btn {
			width: 60px;
			height: 40px;
		}
		&__jackpot-info {
			min-width: 180px;
			height: 30px;
			font-size: 14px;
		}
		&__title {
			font-size: 12px;
		}
	}
}
