.bonus-page {
	margin-bottom: 45px;
	&__grid {
		--min-width: 320px;
		display: grid;
		justify-content: center;
		justify-items: center;
		grid-template-columns: repeat(auto-fit, minmax(var(--min-width), 1fr));
		gap: 10px 0;
		padding-bottom: 25px;
	}
	.tab {
		&__menu {
			gap: 15px;
		}
		&__menu-item {
			flex-grow: unset;
			.h2 {
				padding-right: 0;
			}
		}
	}
	@media screen and (min-width: 760px) {
		&__grid {
			--min-width: 630px;
			grid-template-columns: repeat(auto-fit, var(--min-width));
		}
	}
	@media screen and (min-width: 1800px) {
		&__grid {
			--min-width: 50%;
			gap: 20px 0;
			margin: 0 10px;
			grid-template-columns: repeat(auto-fit, var(--min-width));
		}
		.bonus-card {
			padding: 0 10px;
			max-width: 960px;
			&__img-box {
				width: 50%;
				height: 310px;
			}
			&__content {
				grid-template-rows: 1fr 60px;
				padding: 20px 50px 20px;
			}
			&__info-box {
				font-size: 14px;
			}
			&__info--big {
				font-size: 1.571428em;
			}
		}
	}
}