.main-nav {
	--main-nav-font-color: var(--gray);
	--main-nav-link-bg: transparent;
	position: relative;
	display: flex;
	justify-content: center;
	align-items: flex-end;
	width: 100%;
	&__list {
		display: flex;
		justify-content: flex-start;
		align-items: flex-end;
		padding: 0 5px;
		width: auto;
		overflow-y: hidden;
		overflow-x: auto;
	}
	&__item {
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 0 1px;
	}
	&__link {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		padding: 0 14px;
		width: auto;
		min-width: 50px;
		height: 20px;
		font-size: 10px;
		font-weight: 400;
		color: var(--main-nav-font-color);
		background: var(--main-nav-link-bg);
		border-radius: 4px;
		&:hover {
			--main-nav-font-color: var(--white);
			--main-nav-link-bg: var(--gray-dark);
		}
		&--active {
			--main-nav-font-color: var(--gray-dark);
			--main-nav-link-bg: var(--white-dark);
			&:hover {
				--main-nav-font-color: var(--gray-dark);
				--main-nav-link-bg: var(--white-dark);
			}
		}
		&--row-reverse {
			flex-direction: row-reverse;
			padding: 0 0 0 10px;
		}
		&--column {
			flex-direction: column;
			padding: 0 0 10px 0;
		}
		&--column-revers {
			flex-direction: column-reverse;
			padding: 10px 0 0 0;
		}
	}
	&__img {
		display: none;
		visibility: hidden;
	}
	&__info {
		display: inline-block;
		text-transform: uppercase;
		white-space: nowrap;
	}
	@media screen and (min-width: 1800px) {
		&__item  {
			height: 60px;
		}
		&__link {
			padding: 0 20px;
			height: 30px;
			font-size: 14px;
			font-weight: 700;
			&--active {
				// transform: translateY(7px);
				// border-radius: 4px 4px 0 0;
				// box-shadow: 0px 8px 0px 0px var(--main-nav-link-bg);
			}
		}
	}
}
