.footer {
	--footer-padding-bottom: 100px;
	// --footer-padding-x: 10px;
	--footer-bg: var(--bg-color);
	--footer-color: var(--white-dark);
	display: block;
	padding: 25px 0 var(--footer-padding-bottom);
	color: var(--footer-color);
	background: var(--footer-bg);
	&__nav {
		display: none;
		visibility: hidden;
	}
	&__contacts {
		display: block;
	}
	&__partners {
		display: block;
	}
	&__info-box {
		display: block;
		padding: 15px 0;
		font-size: 10px;
		line-height: 14px;
		color: var(--gray);
		text-align: center;
		border-top: 1px solid var(--gray-dark);
		border-bottom: 1px solid var(--gray-dark);
	}
	&__service-box {
		display: block;
	}
	@media screen and (min-width: 600px) {
		--footer-padding-bottom: 0;
	}
	@media screen and (min-width: 1300px) {
		&__nav {
			display: block;
			visibility: visible;
		}
	}
	@media screen and (min-width: 1800px) {
		&__info-box {
			font-size: 12px;
		}
	}
}
.footer-nav {
	--footer-nav-color: var(--gray);
	--footer-nav-bg: var(--black-dark);
	display: block;
	padding: 10px 0;
	background: var(--footer-nav-bg);
	&__list {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		gap: 10px 0;
	}
	&__item {
		position: relative;
		padding: 0 15px;
		&::after {
			content: "|";
			display: block;
			position: absolute;
			top: 50%;
			right: 0;
			transform: translateY(-50%);
			font-size: 12px;
			font-weight: 700;
			color: var(--gray-dark);
		}
		&:last-child {
			&::after {
				content: none;
			}
		}
	}
	&__link {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		gap: 5px;
		padding: 0 7px;
		min-height: 20px;
		font-size: 10px;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		color: var(--footer-nav-color);
		border-radius: 4px;
		&:hover {
			color: var(--white);
			background-color: var(--gray-dark);
		}
		&--current {
			color: var(--font-color);
			background-color: var(--white-dark);
		}
	}
	&__link-img {
		&:empty {
			display: none;
			visibility: hidden;
		}
	}
	&__link-info {
		&:empty {
			display: none;
			visibility: hidden;
		}
		&::first-letter {
			text-transform: capitalize;
		}
	}
	@media screen and (min-width: 1800px) {
		&__link {
			padding: 0 15px;
			min-height: 30px;
			font-size: 12px;
		}
	}
}
.footer-contacts {
	--footer-contacts-link-color: var(--gray);
	display: block;
	padding: 20px 0;
	border-top: 1px solid var(--gray-dark);
	border-bottom: 1px solid var(--gray-dark);
	&__list {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		gap: 10px;
	}
	&__link {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		gap: 5px;
		padding: 0 10px;
		font-size: 12px;
		color: var(--footer-contacts-link-color);
		&:hover {
			color: var(--decor-color);
			.footer-contacts__link-img {
				opacity: 1;
			}
		}
	}
	&__link-img {
		display: block;
		max-width: 30px;
		width: 30px;
		height: auto;
		opacity: 0.6;
	}
	&__link-info {
		&:empty {
			display: none;
			visibility: hidden;
		}
	}
	@media screen and (min-width: 1800px) {
		&__list {
			gap: 10px 50px;
		}
		&__link {
			font-size: 14px;
		}
	}
}

.footer-partners {
	display: block;
	padding: 10px 0;
	&__slider {
		display: none;
		visibility: hidden;
		&.slick-initialized {
			display: block;
			visibility: visible;
		}
		.slick-list {
			-webkit-mask-image: linear-gradient(
				90deg,
				#fff,
				#fff 2%,
				#fff 97%,
				hsla(0deg, 0%, 100%, 0)
			);
			mask-image: linear-gradient(
				90deg,
				#fff,
				#fff 2%,
				#fff 97%,
				hsla(0deg, 0%, 100%, 0)
			);
		}
	}
	&__item {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		padding: 0 5px;
	}
	&__link {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		padding: 5px;
		filter: grayscale(1);
		&:hover {
			filter: grayscale(0);
		}
	}
	&__link-img {
		display: block;
		width: auto;
		height: 30px;
	}
	&__link-info {
		&:empty {
			display: none;
			visibility: hidden;
		}
	}
}
.footer-service {
	display: block;
	padding: 15px 0;
	&__list {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		gap: 10px;
	}
	&__link {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		gap: 5px;
	}
	&__link-img {
		display: block;
		max-height: 36px;
		width: auto;
		// &:empty {
		// 	display: none;
		// 	visibility: hidden;
		// }
	}
	&__link-info {
		&:empty {
			display: none;
			visibility: hidden;
		}
	}
}
.footer-rights {
	display: block;
	text-align: center;
	padding: 10px;
	font-size: 12px;
	color: var(--gray);
	&__row {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		gap: 5px;
	}
	&__logo {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		text-transform: uppercase;
		gap: 5px;
	}
	&__img {
		display: inline-block;
		height: 16px;
	}
}
