.fast-dep {
	display: flex;
	flex-direction: column;
	align-items: center;
    &__title {
		display: block;
		margin-bottom: 10px;
		padding: 0 20px;
		max-width: 500px;
		width: 100%;
		font-weight: 700;
		text-align: center;
    }
    &__form {
		display: inline-block;
		max-width: 340px;
		width: 100%;
		padding: 20px;
		background: var(--white-dark);
    }
	@media screen and (min-width: 360px) {
		&__form {
			padding: 20px 30px 25px;
		}
	}
	@media screen and (min-width: 600px) {
		&__form {
			max-width: 420px;
			padding: 25px 50px 30px;
		}
	}
	@media screen and (min-width: 1800px) {
		&__title {
			padding: 20px;
		}
		&__form {
			max-width: 500px;
		}
	}
}
