.mobile-menu {
	--action-area-bg: var(--black);
	--nav-bg: var(--gray-dark);
	position: fixed;
	top: 0;
	left: 0;
	display: grid;
	grid-template-rows: 40px 1fr;
	max-width: 360px;
	width: 100vw;
	height: 100vh;
	z-index: 9;
	pointer-events: none;
	transform: translateX(-120%);
	transition: transform 0.3s ease-in-out;
	&--active {
		pointer-events: all;
		transform: translateX(0);
	}
	&__action-area {
		display: flex;
		align-items: center;
		gap: 10px;
		padding: 0 10px;
		background: var(--action-area-bg);
		.small-btn--black {
			--btn-font-color: var(--white);
		}
	}
	&__log-reg-area {
		flex: 1;
	}
	&__nav {
		display: block;
		padding: 0 0 60px;
		background: var(--nav-bg);
		overflow-y: auto;
	}
	&__row {
		display: flex;
		// margin: 0 -10px;
	}
	&__list {
		--list-bg: var(--black);
		flex: 1;
		padding: 10px;
		background: var(--list-bg);
		&--white {
			--list-bg: var(--white-dark);
		}
		&--transparent {
			--list-bg: transparent;
		}
	}
	&__item {
		position: relative;
		margin-bottom: 5px;
		&:last-child {
			margin-bottom: 0;
		}
		&--sub-menu {
			&::after {
				content: "";
				display: block;
				position: absolute;
				top: 14px;
				right: 16px;
				width: 0;
				height: 0;
				border-style: solid;
				border-width: 4px 4px 0 4px;
				border-color: var(--gray) transparent transparent
					transparent;
				pointer-events: none;
				transition: transform 0.2s ease-in-out;
			}
		}
		&--active {
			&::after {
				transform: rotate(180deg);
			}
		}
	}
	.registration-link {
		display: none;
		visibility: hidden;
	}
	@media screen and (min-width: 360px) {
		.registration-link {
			display: inline-flex;
			visibility: visible;
		}
	}
}
.sub-menu {
	--sub-menu-bg: var(--white);
	--sub-menu-link-hover-bg: var(--white-dark);
	--sub-menu-link-color: var(--gray);
	--sub-menu-link-color-hover: var(--gray-dark);
	display: none;
	visibility: hidden;
	padding: 10px 20px 0;
	&--active {
		display: block;
		visibility: visible;
	}
	&__item {
		margin-bottom: 3px;
	}
	&__link {
		position: relative;
		display: flex;
		align-items: center;
		padding-left: 40px;
		padding-right: 10px;
		width: 100%;
		height: 30px;
		font-size: 10px;
		color: var(--sub-menu-link-color);
		border-radius: 4px;
		cursor: pointer;
		&--current,
		&:hover {
			background: var(--sub-menu-link-hover-bg);
			color: var(--sub-menu-link-color-hover);
		}
	}
	&__link-info {
		display: inline-block;
		text-transform: uppercase;
	}
}
.mobile-lang {
	--mobile-lang-bg: transparent;
	--lang-font-color: var(--gray);
	--lang-link-bg-hover: var(--white-dark);
	position: relative;
	display: flex;
	flex-direction: column;
	padding: 10px 10px 0 30px;
	height: 41px;
	background: var(--mobile-lang-bg);
	border-top: 1px solid var(--black);
	overflow: hidden;
	&::after {
		content: "";
		display: block;
		position: absolute;
		top: 22px;
		right: 26px;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 4px 4px 0 4px;
		border-color: var(--gray) transparent transparent transparent;
		pointer-events: none;
		transition: transform 0.2s ease-in-out;
	}
	// &::before {
	// 	content: "";
	// 	display: block;
	// 	position: absolute;
	// 	top: 0;
	// 	left: -10px;
	// 	right: -10px;
	// 	height: 1px;
	// 	background: var(--black);
	// }
	&--active {
		height: auto;
		&::after {
			transform: rotate(180deg);
		}
		.mobile-lang__link {
			&--current {
				--lang-font-color: var(--white);
			}
		}
	}
	&__item {
		order: 2;
		margin-bottom: 8px;
		&--current {
			--lang-font-color: var(--white);
			order: 1;
		}
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__link {
		display: flex;
		align-items: center;
		padding: 0 5px;
		width: 100%;
		height: 30px;
		font-size: 10px;
		font-weight: 400;
		color: var(--lang-font-color);
		text-transform: capitalize;
		border-radius: 4px;
		cursor: pointer;
		&:hover {
			--lang-font-color: var(--black);
			background: var(--lang-link-bg-hover);
		}
	}
	&__img {
		display: block;
		width: 30px;
		height: 30px;
	}
	&__info {
		text-transform: uppercase;
		padding: 0 15px;
	}
}
