.tournament-card {
	--bg-color: var(--white-dark);
	display: block;
	padding: 0 5px;
	max-width: 310px;
	width: 100%;
	color: var(--font-color);
	text-align: center;
	&--vertical {
		padding: 0;
		max-width: 460px;
		.tournament-card {
			&__img-box {
				width: 100%;
			}
			&__content {
				position: relative;
				padding-top: 0;
				min-height: unset;
				z-index: 2;
			}
			&__info-box {
				margin-bottom: 20px;
			}
		}
		.countdown {
			margin-top: -25px;
		}
	}
	&__img-box {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-end;
		padding-bottom: 67.74193%;
		width: 300px;
		height: 0;
	}
	&__img {
		position: absolute;
		top: 0;
		left: 0;
		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	&__content {
		display: grid;
		grid-template-rows: 1fr 40px;
		padding: 20px 15px;
		min-height: 210px;
		background: var(--bg-color);
	}
	&__info-box {
		display: grid;
		justify-content: center;
		align-content: center;
		gap: 20px;
		font-size: 12px;
	}
	&__info {
		display: -webkit-box;
		-webkit-line-clamp: 4;
		-webkit-box-orient: vertical;
		overflow: hidden;
		text-overflow: ellipsis;
		max-height: 5em;
		&--big {
			font-size: 1.33333em;
			line-height: 1;
			font-weight: 700;
		}
	}
	&__btn-row {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		gap: 10px;
	}
