	@media screen and (min-width: 330px) {
		max-width: 320px;
		&--vertical {
			display: block;
			max-width: 460px;
		}
		&__img-box {
			width: 310px;
		}
	}
	@media screen and (min-width: 760px) {
		display: flex;
		max-width: 630px;
		&--vertical {
			display: block;
			max-width: 460px;
		}
		& > * {
			flex: 1;
		}
		&__img-box {
			width: 310px;
			min-width: 310px;
			height: 210px;
			padding-bottom: 0;
		}
		&__content {
			width: 310px;
			min-width: 310px;
		}
	}
	@media screen and (min-width: 1800px) {
		&--vertical {
			display: block;
			width: 460px;
			.tournament-card {
				&__img-box {
					padding-bottom: 67.74193%;
					height: 0;
				}
				&__content {
					width: 100%;
				}
				&__info--big {
					font-size: 1.58em;
				}
			}
		}
		&__info-box {
			gap: 10px;
			font-size: 14px;
		}
	}
}
