.news {
	&__slider {
		margin: 0 -5px;
		display: none;
		visibility: hidden;
		&.slick-initialized {
			display: block;
			visibility: visible;
		}
	}
	&__item {
		display: block;
		padding: 0 5px;
		width: 310px;
		overflow: hidden;
	}
	&__img-box {
		position: relative;
		display: block;
		width: 100%;
		height: 0;
		padding-bottom: 61.3%;
		background-color: var(--gray);
		overflow: hidden;
		&:hover {
			.news__img {
				transform: scale(1.05);
			}
		}
	}
	&__img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
		transform: scale(1);
		transition: transform 0.3s ease-in-out;
	}
	&__info-box {
		display: block;
		padding: 10px 20px;
		width: 100%;
		height: 110px;
		color: var(--font-color);
		background: var(--white-dark);
	}
	&__title-link {
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
		margin-bottom: 10px;
		width: 100%;
		height: 34px;
		font-size: 14px;
		font-weight: 700;
		color: var(--font-color);
		overflow: hidden;
		&:hover {
			color: var(--decor-color);
		}
	}
	&__info {
		display: -webkit-box;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;
		overflow: hidden;
		text-overflow: ellipsis;
		height: 42px;
		font-size: 12px;
	}
	@media screen and (min-width: 330px) {
		&__item {
			width: 320px;
		}
	}
	@media screen and (min-width: 350px) {
		&__slider {
			.slick-list {
				mask-image: linear-gradient(
					90deg,
					#fff,
					#fff 2%,
					#fff 97%,
					hsla(0deg, 0%, 100%, 0)
				);
			}
		}
	}
	@media screen and (min-width: 1800px) {
		&__item {
			padding: 0 7px;
			width: 474px;
		}
		&__info-box {
			height: 130px;
		}
		&__title-link {
			height: 40px;
			font-size: 16px;
		}
		&__info {
			height: 50px;
			font-size: 14px;
		}
	}
	@media screen and (min-width: 1920px) {
		&__slider {
			.slick-list {
				mask-image: none;
			}
		}
	}
}
