.play-game {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: calc(100vh - var(--header-height));
	&--full-screen {
		margin-top: calc(var(--header-height) * -1);
		height: 100vh;
		z-index: 10;
		.play-game {
			&__box {
				width: 100%;
				height: 100%;
			}
		}
	}
	&__img-bg {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 1;
		pointer-events: none;
	}
	&__box {
		position: relative;
		display: block;
		width: calc((100vh - 150px) * 1.77);
		height: calc(100vh - 120px);
		z-index: 2;
	}
	&__header {
		display: flex;
		justify-content: space-between;
		align-items: center;
		gap: 10px;
		padding: 0 10px;
		height: 40px;
		background-color: var(--black);
	}
	&__title {
		display: flex;
		align-items: center;
		gap: 10px;
		font-size: 12px;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		color: var(--gray);
	}
	&__title-img {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 30px;
		min-width: 30px;
		height: 30px;
	}
	&__action-box {
		display: flex;
		align-items: center;
		gap: 5px;
	}
	&__game-box {
		position: relative;
		display: block;
		width: 100%;
		height: calc(100% - 40px);
		background: var(--black-dark);
		& > iframe {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}
	}
	&__mobile {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
	}
	&__mobile-btn-box {
		display: block;
		margin: 0 auto;
		padding-top: 15px;
		max-width: 300px;
		width: 100%;
	}
	&__mobile-btn-row {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		gap: 20px;
		padding: 20px 0;
	}
	&__btn {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		&--left {
			left: 0;
		}
		&--right {
			right: 0;
		}
	}
	.game-item {
		&__box {
			display: flex;
			flex-direction: column-reverse;
		}
		&__title {
			font-size: 14px;
		}
	}
	@media screen and (min-width: 1800px) {
		&__header {
			height: 60px;
		}
		&__title-img {
			width: 40px;
			min-width: 40px;
			height: 40px;
		}
		&__action-box {
			gap: 10px;
		}
		&__game-box {
			height: calc(100% - 60px);
		}
	}
}
