.payment-catalog {
	display: block;
	width: 100%;
	background: var(--gray-dark);
	&__grid {
		display: grid;
		grid-template-columns: repeat(auto-fill, minmax(106px, 1fr));
		justify-items: center;
		justify-content: center;
		padding: 10px;
	}
	@media screen and (min-width: 760px) {
		&__grid {
			grid-template-columns: repeat(auto-fill, 160px);
		}
	}
}
