.countdown {
	--bg-color: var(--gray-dark);
	--font-color: var(--white-dark);
	display: inline-flex;
	justify-content: center;
	align-items: center;
	gap: 20px;
	padding: 0 15px;
	width: auto;
	color: var(--font-color);
	background: var(--bg-color);
	border-radius: 4px;
    &__item {
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: flex-end;
		width: 30px;
		height: 55px;
		&::after {
			content: ":";
			position: absolute;
			top: 6px;
			right: 0;
			transform: translate(13px, 0);
			font-size: 18px;
			font-weight: 700;
		}
		&:first-child,
		&:last-child {
			&::after {
				content: none;
			}
		}
	}
    &__count {
		display: flex;
		justify-content: center;
		align-items: center;
		padding-top: 5px;
		height: 30px;
		font-size: 18px;
		line-height: 1;
    }
    &__name {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 25px;
		font-size: 10px;
		text-transform: uppercase;
		color: var(--gray);
		border: 3px solid transparent;
		border-top-color: var(--decor-color);
    }
}
