.them-filter {
	// 7b1fa2
	--them-filter-bg: var(--white-dark);
	--link-color: var(--black);
	position: relative;
	z-index: 2;
	display: flex;
	justify-content: center;
	width: 100%;
	background: var(--them-filter-bg);
	&__list {
		display: flex;
		justify-content: flex-start;
		align-items: center;
		width: auto;
		padding: 0 3px;
		text-align: center;
		overflow-y: hidden;
		overflow-x: auto;
	}
	&__item {
		position: relative;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		padding: 0 3px;
	}
	&__link {
		display: inline-flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		height: 30px;
		&:hover {
			.them-filter__link-info {
				color: var(--white);
				background: var(--gray);
			}
		}
		&--active {
			.them-filter__link-info {
				color: var(--white);
				background: var(--gray-dark);
				box-shadow: none;
			}
			&:hover {
				.them-filter__link-info {
					color: var(--white);
					background: var(--gray-dark);
					box-shadow: none;
				}
			}
		}
		&--sub-menu {
			.them-filter__link-info {
				position: relative;
				padding-right: 25px;
				&::before,
				&::after {
					content: "";
					display: block;
					position: absolute;
					top: 50%;
				}
				&::before {
					right: 6px;
					transform: translateY(-50%);
					width: 12px;
					height: 12px;
					background: var(--gray-dark);
					border-radius: 3px;
					z-index: 1;
				}
				&::after {
					right: 9px;
					transform: translateY(-25%);
					width: 0;
					height: 0;
					border-style: solid;
					border-width: 3px 3px 0 3px;
					border-color: var(--white) transparent transparent
						transparent;
					z-index: 2;
				}
			}
			&:hover,
			&.them-filter__link--active {
				.them-filter__link-info {
					padding-right: 25px;
				}
			}
			&.them-filter__link--active {
				.them-filter__link-info {
					&::before {
						background: var(--blue);
					}
				}
			}
		}
	}
	&__link-info {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		padding: 3px 15px;
		height: 20px;
		font-size: 10px;
		font-weight: 400;
		color: var(--link-color);
		text-transform: uppercase;
		white-space: nowrap;
		border-radius: 4px;
		background: transparent;
	}
	&__sub-menu {
		--sub-menu-bg: var(--black-dark);
		position: absolute;
		top: 100%;
		left: 0;
		width: 100%;
		max-height: calc(100vh - 100px);
		height: auto;
		display: none;
		visibility: hidden;
		background: var(--sub-menu-bg);
		overflow-y: auto;
		&--active {
			display: block;
			visibility: visible;
		}
	}
	&__catalog {
		display: none;
		visibility: hidden;
		&--active {
			display: block;
			visibility: visible;
		}
	}
	@media screen and (min-width: 1800px) {
		&__link {
			height: 40px;
			&--active {
				// transform: translateY(5px);
				.them-filter__link-info {
					background: var(--black-dark);
					// border-radius: 4px 4px 0 0;
				}
				&:hover {
					.them-filter__link-info {
						background: var(--black-dark);
					}
				}
			}
			&--sub-menu {
				.them-filter__link-info {
					padding-right: 45px;
					&::before {
						right: 18px;
						width: 20px;
						height: 20px;
					}
					&::after {
						right: 23px;
						border-width: 5px 5px 0 5px;
					}
				}
				&:hover,
				&.them-filter__link--active {
					.them-filter__link-info {
						padding-right: 45px;
					}
				}
			}
		}
		&__link-info {
			padding: 0 20px;
			height: 30px;
			font-size: 14px;
			font-weight: 700;
		}
	}
}

.them-catalog {
	display: block;
	padding: 10px 0 20px;
	width: 100%;
	&__slider {
		display: none;
		visibility: hidden;
		&.slick-initialized {
			display: block;
			visibility: visible;
		}
		.slick-list {
			padding: 0 30px;
			mask-image: linear-gradient(
				90deg,
				hsla(0, 0%, 100%, 0),
				#fff 3%,
				#fff 97%,
				hsla(0, 0%, 100%, 0)
			);
			@media screen and (min-width: 1300px) {
				mask-image: linear-gradient(
					90deg,
					hsla(0, 0%, 100%, 0),
					#fff 3%,
					#fff 97%,
					hsla(0, 0%, 100%, 0)
				);
			}
			@media screen and (min-width: 1600px) {
				mask-image: linear-gradient(
					90deg,
					hsla(0, 0%, 100%, 0),
					#fff 5%,
					#fff 95%,
					hsla(0, 0%, 100%, 0)
				);
			}
		}
	}
	&__list {
		display: grid;
		grid-template-columns: repeat(auto-fill, 110px);
		justify-content: center;
		gap: 10px 0;
		&--scroll {
			justify-content: start;
			grid-template-columns: repeat(10, 130px);
			grid-template-rows: 120px 120px;
			grid-auto-flow: column;
			overflow-x: auto;
			-webkit-mask-image: linear-gradient(
				90deg,
				#fff,
				#fff 3%,
				#fff 97%,
				hsla(0deg, 0%, 100%, 0)
			);
			mask-image: linear-gradient(
				90deg,
				#fff,
				#fff 3%,
				#fff 97%,
				hsla(0deg, 0%, 100%, 0)
			);
		}
	}
	&__item {
		display: inline-flex;
		padding: 0 10px;
	}
	&__link {
		position: relative;
		display: inline-flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		padding: 5px 5px 0px 5px;
		width: 90px;
		background: var(--black);
		border-radius: 4px;
		&:hover,
		&--active {
			background: var(--white-dark);
			.them-catalog {
				&__link-info {
					color: var(--font-color);
				}
			}
		}
	}
	&__link-img {
		display: block;
		width: 80px;
		height: 60px;
		object-fit: cover;
	}
	&__link-info {
		display: inline-flex;
		align-items: center;
		width: 80px;
		height: 25px;
		font-size: 10px;
		font-weight: 700;
		color: var(--gray);
		text-transform: uppercase;
		text-align: center;
		overflow: hidden;
		span {
			display: block;
			width: 80px;
			text-align: center;
			text-transform: uppercase;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
		}
	}
	@media screen and (min-width: 1800px) {
		padding: 30px 0;
		&__list {
			grid-template-columns: repeat(auto-fill, 140px);
			gap: 20px;
		}
		&__link {
			width: 140px;
		}
		&__link-img {
			width: 130px;
			height: 100px;
		}
		&__link-info {
			width: 130px;
			height: 35px;
			font-size: 12px;
			span {
				width: 130px;
			}
		}
	}
}

.provider-catalog {
	--link-img-bg: var(--black);
	display: block;
	&__grid {
		display: flex;
		flex-wrap: wrap;
		gap: 15px 10px;
		padding: 20px 0;
		width: 100%;
	}
	&__slider {
		display: none;
		visibility: hidden;
		&.slick-initialized {
			display: block;
			visibility: visible;
		}
		.slick-list {
			padding: 0 30px;
			mask-image: linear-gradient(
				90deg,
				hsla(0, 0%, 100%, 0),
				#fff 3%,
				#fff 97%,
				hsla(0, 0%, 100%, 0)
			);
			@media screen and (min-width: 1300px) {
				mask-image: linear-gradient(
					90deg,
					hsla(0, 0%, 100%, 0),
					#fff 3%,
					#fff 97%,
					hsla(0, 0%, 100%, 0)
				);
			}
			@media screen and (min-width: 1600px) {
				mask-image: linear-gradient(
					90deg,
					hsla(0, 0%, 100%, 0),
					#fff 5%,
					#fff 95%,
					hsla(0, 0%, 100%, 0)
				);
			}
		}
		.provider-catalog {
			&__item {
				padding-left: 5px;
				padding-right: 5px;
			}
		}
	}
	&__item {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		padding-top: 3px;
	}
	&__link {
		position: relative;
		display: inline-flex;
		align-items: center;
		gap: 5px;
		padding: 0 5px;
		width: auto;
		height: 40px;
		background: var(--black);
		border-radius: 4px;
		cursor: pointer;
		&:hover,
		&--active {
			background: var(--white-dark);
			.provider-catalog {
				&__link-info {
					color: var(--font-color);
				}
			}
		}
	}
	&__link-img-box {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 30px;
		height: 30px;
		border-radius: 50%;
	}
	&__link-img {
		display: block;
		width: inherit;
		height: inherit;
		object-fit: cover;
	}
	&__link-info {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		padding: 0 5px;
		width: auto;
		font-size: 10px;
		color: var(--gray);
		text-transform: uppercase;
	}
	&__link-count {
		position: absolute;
		top: -3px;
		left: 25px;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		padding: 0 3px;
		width: auto;
		min-width: 20px;
		height: 12px;
		font-size: 10px;
		color: var(--gray);
		background: var(--gray-dark);
		border-radius: 3px;
		z-index: 2;
	}
	@media screen and (min-width: 1800px) {
		&__item {
			padding-top: 6px;
		}
		&__link {
			height: 50px;
		}
		&__link-img-box {
			width: 40px;
			height: 40px;
		}
		&__link-info {
			font-size: 12px;
		}
		&__link-count {
			top: -6px;
			left: 35px;
			min-width: 26px;
			height: 16px;
			font-size: 12px;
		}
	}
}
