.promo-slider {
	display: none;
	visibility: hidden;
	margin: 0 -5px;
	&.slick-initialized {
		display: block;
		visibility: visible;
	}
	@media screen and (min-width: 340px) {
		.slick-list {
			mask-image: linear-gradient(
				90deg,
				#fff,
				#fff 2%,
				#fff 97%,
				hsla(0deg, 0%, 100%, 0)
			);
		}
	}
	@media screen and (min-width: 1920px) {
		.slick-list {
			mask-image: none;
		}
	}
}
