.small-btn {
	--btn-font-color: var(--white);
	--btn-bg-color: var(--decor-color);
	position: relative;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	padding: 0 15px;
	width: auto;
	height: 30px;
	font-size: 12px;
	font-weight: 700;
	color: var(--btn-font-color);
	background: var(--btn-bg-color);
	border-radius: 4px;
	cursor: pointer;
	&__info,
	span {
		display: inline-block;
		&::first-letter {
			text-transform: capitalize;
		}
	}
	&:hover {
		box-shadow: inset 2px 2px 0px 2px rgba(255, 255, 255, 0.2),
			inset 5px 5px 0px 2px rgba(255, 255, 255, 0.2),
			inset -2px -2px 0px 2px rgba(255, 255, 255, 0.2),
			inset -5px -5px 0px 2px rgba(255, 255, 255, 0.2);
	}
	&--black {
		--btn-font-color: var(--white);
		--btn-bg-color: var(--gray-dark);
	}
	&--white {
		--btn-font-color: var(--font-color);
		--btn-bg-color: var(--white-dark);
	}
	&--big {
		width: 100%;
	}
	&--small {
		padding: 0;
		// max-width: 30px;
		width: auto;
		min-width: 30px;
	}
	&.btn-chat {
		position: absolute;
		bottom: 60px;
		right: 0;
		justify-content: flex-start;
		gap: 10px;
		min-width: 140px;
		text-transform: uppercase;
		border-radius: 4px 0 0 4px;
		box-shadow: -1px -1px 10px 0px var(--bg-color);
		z-index: 8;
		// &:hover {
		// }
	}
	&__img {
		display: block;
		width: 30px;
		height: 30px;
	}
	&--active {
		.small-btn-st0 {
			fill: var(--decor-color);
		}
	}
	@media screen and (min-width: 600px) {
		&.btn-chat {
			position: fixed;
			bottom: 0;
			border-radius: 4px 0 0 0;
		}
	}
	@media screen and (min-width: 1800px) {
		padding: 0 25px;
		height: 40px;
		font-size: 14px;
		&.btn-chat {
			min-width: 210px;
			.small-btn__img {
				display: block;
				width: 40px;
				height: 40px;
			}
		}
	}
}

.btn {
	--btn-font-color: var(--white);
	--btn-bg-color: var(--decor-color);
	// --btn-shadow-top: var(--decor-color);
	// --btn-shadow-bottom: var(--decor-color);
	position: relative;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	padding: 0 25px;
	width: auto;
	// min-width: 200px;
	height: 40px;
	font-size: 14px;
	font-weight: 700;
	color: var(--btn-font-color);
	// text-transform: capitalize;
	background: var(--btn-bg-color);
	border-radius: 4px;
	cursor: pointer;
	span::first-letter {
		text-transform: capitalize;
	}
	&:hover {
		box-shadow: inset 2px 2px 0px 2px rgba(255, 255, 255, 0.2),
			inset 5px 5px 0px 2px rgba(255, 255, 255, 0.2),
			inset -2px -2px 0px 2px rgba(255, 255, 255, 0.2),
			inset -5px -5px 0px 2px rgba(255, 255, 255, 0.2);
	}
	&--black {
		--btn-font-color: var(--white);
		--btn-bg-color: var(--gray-dark);
	}
	&--medium {
		min-width: 160px;
	}
	&--small {
		max-width: 90px;
		min-width: 90px;
	}

	&__img {
		display: block;
		margin-right: 10px;
		width: 30px;
		height: 30px;
	}
	input[type="submit"] {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
		z-index: 2;
	}
	@media screen and (min-width: 1800px) {
		padding: 0 35px;
		height: 50px;
		font-size: 16px;
		&--medium {
			min-width: 200px;
		}
	}
}

.nav-link {
	--nav-link-color: var(--gray);
	--nav-link-color-hover: var(--gray-dark);
	--nav-link-bg: transparent;
	--nav-link-bg-hover: var(--white-dark);
	position: relative;
	display: flex;
	align-items: center;
	padding: 0 10px 0 30px;
	width: auto;
	min-width: 100px;
	height: 30px;
	font-size: 10px;
	font-weight: 400;
	color: var(--nav-link-color);
	background: var(--nav-link-bg);
	border-radius: 4px;

	&.nav-link--dark {
		--nav-link-color: var(--nav-link-color-hover);
		&:hover,
		&.nav-link--current {
			--nav-link-color: var(--white-dark);
			--nav-link-bg: var(--gray-dark);
		}
	}

	&:hover,
	&--current {
		--nav-link-color: var(--font-color);
		--nav-link-bg: var(--nav-link-bg-hover);
	}
	&__img {
		position: absolute;
		top: 0;
		left: 0;
		display: block;
		width: 30px;
		height: 30px;
	}
	&__info {
		display: inline-block;
		white-space: nowrap;
		text-transform: uppercase;
	}
	&__alert {
		position: absolute;
		top: -5px;
		right: -5px;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 16px;
		height: 16px;
		font-size: 10px;
		font-weight: 700;
		color: var(--white);
		border-radius: 50%;
		background: var(--red);
		z-index: 2;
		&:empty {
			display: none;
			visibility: hidden;
		}
	}
}

.btn-close {
	--btn-font-color: var(--white-dark);
	--btn-bg-color: var(--gray-dark);
	position: relative;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	width: 30px;
	height: 30px;
	background: var(--btn-bg-color);
	border-radius: 4px;
	transition: box-shadow 0.4s ease-in-out;
	cursor: pointer;
	&:hover {
		--btn-bg-color: var(--decor-color);
	}
	&__img {
		display: block;
		width: 24px;
		height: 24px;
	}
}

.action-btn {
	--btn-font-color: var(--gray);
	--btn-bg-color: var(--gray-dark);
	position: relative;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	min-width: 30px;
	width: 30px;
	height: 30px;
	font-size: 12px;
	font-weight: 700;
	color: var(--btn-font-color);
	background: var(--btn-bg-color);
	border-radius: 4px;
	cursor: pointer;
	&__info,
	span {
		display: inline-block;
		&::first-letter {
			text-transform: capitalize;
		}
	}
	&__img,
	svg,
	img {
		display: block;
		width: 30px;
		height: 30px;
	}
	.action-btn-st0 {
		fill: var(--white-dark);
	}
	&--active,
	&:hover {
		--btn-bg-color: var(--decor-color);
	}
	@media screen and (min-width: 1800px) {
		min-width: 40px;
		width: 40px;
		height: 40px;
	}
}

.btn-row {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	gap: 15px;
	padding: 15px 0;
	&--no-padding {
		display: inline-flex;
		flex-wrap: nowrap;
		padding: 0;
	}
	@media screen and (min-width: 1800px) {
		gap: 20px;
		padding: 20px 0;
	}
}
