.payment-system {
	display: block;
	&__slider {
		margin: 0 -5px;
		display: none;
		visibility: hidden;
		&.slick-initialized {
			display: block;
			visibility: visible;
		}
		.slick-list {
			mask-image: linear-gradient(
				90deg,
				hsla(0, 0%, 100%, 0),
				#fff 3%,
				#fff 97%,
				hsla(0, 0%, 100%, 0)
			);
			@media screen and (min-width: 1300px) {
				mask-image: linear-gradient(
					90deg,
					hsla(0, 0%, 100%, 0),
					#fff 3%,
					#fff 97%,
					hsla(0, 0%, 100%, 0)
				);
			}
			@media screen and (min-width: 1600px) {
				mask-image: linear-gradient(
					90deg,
					hsla(0, 0%, 100%, 0),
					#fff 5%,
					#fff 95%,
					hsla(0, 0%, 100%, 0)
				);
			}
		}
	}
	&__item {
		display: flex;
		justify-content: center;
		align-items: center;
		// width: 100%;
		padding: 0 5px;
	}
	&__link {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		&:hover {
			.payment-system__img-box {
				background: var(--white-dark);
			}
		}
	}
	&__img-box {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		width: 100px;
		height: 40px;
		background: var(--black);
		border-radius: 4px;
	}
	&__img {
		display: block;
		max-width: 90px;
		width: 100%;
		max-height: 34px;
		height: auto;
	}
	@media screen and (min-width: 1800px) {
		&__item {
			padding: 0 10px;
		}
		&__img-box {
			width: 140px;
			height: 50px;
		}
		&__img {
			max-width: 130px;
			max-height: 40px;
		}
	}
}
