	// 		transition: transform 0.2s ease-in-out;
	// 	}
	// 	&:hover {
	// 		text-decoration: none;
	// 		&::after {
	// 			transform: translateX(0.2em);
	// 		}
	// 	}
	// }
	@media screen and (min-width: 330px) {
		max-width: 320px;
		&__img-box {
			width: 310px;
		}
	}
	@media screen and (min-width: 760px) {
		display: flex;
		max-width: 630px;
		&--vertical {
			display: block;
			max-width: 320px;
			.bonus-card__img-box {
				width: 310px;
			}
		}
		& > * {
			flex: 1;
		}
		&__img-box {
			width: 310px;
			min-width: 310px;
			height: 210px;
			padding-bottom: 0;
		}
		&__content {
			width: 310px;
			min-width: 310px;
		}
	}
	@media screen and (min-width: 1800px) {
		&--vertical {
			display: block;
			padding: 0 10px;
			max-width: 480px;
			.bonus-card {
				&__img-box {
					padding-bottom: 67.74193%;
					width: 460px;
					height: 0;
				}
				&__content {
					width: 100%;
				}
			}
		}
		&__info-box {
			gap: 10px;
			font-size: 14px;
		}
	}
}

.bonus-info {
	display: block;
	max-width: 640px;
	width: 100%;
	// font-size: 12px;
	line-height: 1.667;
	&__content {
		display: block;
		margin: 0 auto;
		max-width: 640px;
		width: 100%;
	}
	&__img-box {
		display: flex;
		justify-content: center;
		align-items: flex-end;
		width: 100%;
	}
	&__img {
		display: block;
		max-width: 640px;
		width: calc(100% + 30px);
	}
	&__title-box {
		display: block;
		margin-bottom: 15px;
		padding: 10px;
		font-size: 18px;
		line-height: 1;
		font-weight: 700;
		color: var(--white-dark);
		text-align: center;
		background: var(--gray-dark);
	}
	&__title {
		&--big {
			font-size: 2.5em;
			// font-weight: 700;
		}
	}
	&__info {
		display: block;
		margin-bottom: 2em;
		a {
			color: var(--decor-color);
			// font-weight: 700;
			text-decoration: underline;
			&:hover {
				text-decoration: none;
			}
		}
	}
	&__table {
		margin-bottom: 25px;
	}
	&__rules {
		counter-reset: item;
		li {
			margin-bottom: 1em;
			&::before {
				counter-increment: item;
				content: counter(item) ". ";
				display: inline-block;
				margin-right: 5px;
				color: inherit;
				font-weight: 700;
			}
		}
		a {
			color: var(--decor-color);
			// font-weight: 700;
			text-decoration: underline;
			&:hover {
				text-decoration: none;
			}
		}
	}
	&__link-close {
		font-size: 12px;
		color: var(--font-color);
		letter-spacing: 0.1em;
		text-transform: uppercase;
		text-decoration: underline;
		&:hover {
			text-decoration: none;
		}
	}
	@media screen and (min-width: 1200px) {
		&__title-box {
			padding: 20px;
		}
	}
	@media screen and (min-width: 1800px) {
		max-width: 1236px;
		font-size: 14px;
		&__content {
			max-width: 940px;
		}
		&__title-box {
			font-size: 22px;
		}
		&__title {
			&--big {
				font-size: 3.33333em;
			}
		}
	}
}
