.header {
	position: relative;
	&__fixed {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: var(--header-height);
		z-index: 8;
		transition: transform 0.3s ease-in-out;
		box-shadow: 0px 1px 10px 0px var(--bg-color);
		&--hide {
			transform: translateY(-120%);
		}
	}
	&__top {
		--header-top-bg: var(--black);
		position: relative;
		display: block;
		width: 100%;
		height: 40px;
		background: var(--header-top-bg);
		// z-index: 1;
	}
	&__row {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 0 10px;
		height: 40px;
	}
	&__item {
		flex-basis: 50%;
		display: flex;
		align-items: center;
		gap: 10px;
		&--left {
			justify-content: flex-start;
			padding-right: 50px;
		}
		&--right {
			justify-content: flex-end;
			padding-left: 50px;
		}
	}
	&__center {
		--header-center-bg: var(--white-dark);
		position: relative;
		display: flex;
		align-items: flex-start;
		width: 100%;
		height: 30px;
		background: var(--header-center-bg);
		// z-index: 2;
	}
	&__deposit-link {
		display: none;
		visibility: hidden;
	}
	.lang {
		display: none;
		visibility: hidden;
	}
	.main-nav {
		display: none;
		visibility: hidden;
	}
	.registration-link {
		display: none;
		visibility: hidden;
	}
	.user {
		&__short-info {
			padding-right: 6px;
		}
		&__img-box,
		&__img {
			cursor: pointer;
		}
	}
	.small-btn--black {
		--btn-font-color: var(--white);
	}
	@media screen and (min-width: 360px) {
		.lang {
			display: flex;
			visibility: visible;
		}
	}
	@media screen and (min-width: 600px) {
		&__deposit-link {
			display: inline-flex;
			visibility: visible;
		}
		.registration-link {
			display: inline-flex;
			visibility: visible;
		}
		.user {
			&__short-info {
				padding-right: 15px;
			}
			&__value-side {
				display: block;
			}
		}
	}
	@media screen and (min-width: 1200px) {
		&__item {
			&--left {
				padding-right: 10px;
			}
			&--right {
				padding-left: 10px;
			}
		}
		.hamburger {
			display: none;
			visibility: hidden;
		}
		.main-nav {
			display: flex;
			visibility: visible;
		}
	}
	@media screen and (min-width: 1800px) {
		&__top {
			height: 60px;
		}
		&__row {
			padding: 0 20px;
			height: 60px;
		}
		&__center {
			height: 40px;
		}
		.small-btn {
			font-size: 16px;
		}
	}
}

.hamburger {
	--hamburger-bg: var(--gray-dark);
	--line-bg: var(--white-dark);
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	width: 30px;
	min-width: 30px;
	height: 30px;
	border-radius: 4px;
	background: var(--hamburger-bg);
	cursor: pointer;
	&:hover {
		--hamburger-bg: var(--blue);
	}
	&--active {
		.hamburger {
			&__line {
				&--top {
					transform: translateX(3px) rotate(45deg);
				}
				&--center {
					transform: scaleX(0);
				}
				&--bottom {
					transform: translate(1px, 1px) rotate(-45deg);
				}
			}
		}
	}
	&__line {
		display: block;
		width: 16px;
		height: 2px;
		background: var(--line-bg);
		transition: transform 0.2s ease-in-out;
		&--top {
			transform-origin: top left;
		}
		&--center {
			margin: 3px 0;
		}
		&--bottom {
			transform-origin: top left;
		}
	}
}

.logo {
	--logo-font-color: var(--white-dark);
	--logo-bg: var(--bg-color);
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	padding: 0 5px;
	// background: var(--logo-bg);
	&--fixed {
		position: absolute;
		top: 5px;
		left: 50%;
		transform: translateX(-50%);
		z-index: 2;
	}
	&__img {
		display: block;
		width: 45px;
		height: 30px;
	}
	&__info {
		font-size: 12px;
		font-weight: 700;
		color: var(--logo-font-color);
		text-transform: uppercase;
	}
	@media screen and (min-width: 1200px) {
		&--fixed {
			position: static;
			top: unset;
			left: unset;
			transform: translateX(0);
		}
	}
	@media screen and (min-width: 1800px) {
		padding: 0 20px 0 0;
		&__img {
			width: 90px;
			height: 60px;
		}
	}
}

.search-widget-trigger {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 30px;
	min-width: 30px;
	height: 30px;
	background: var(--gray-dark);
	border-radius: 50%;
	cursor: pointer;
	&:hover {
		background: var(--decor-color);
	}

	&__img {
		display: block;
		width: 24px;
		height: 24px;
	}
	&__info {
		display: none;
		visibility: hidden;
		&:empty {
			display: none;
			visibility: hidden;
		}
	}
	@media screen and (min-width: 600px) {
		&--big {
			padding: 0 3px;
			width: auto;
			border-radius: 4px;
			.search-widget-trigger__info {
				display: block;
				visibility: visible;
				padding: 0 6px;
				font-size: 12px;
				font-weight: 700;
				color: var(--white-dark);
				white-space: nowrap;
			}
		}
	}
	@media screen and (min-width: 1800px) {
		&--big {
			height: 40px;
			padding: 0 10px;
			.search-widget-trigger__info {
				padding: 0 15px;
				font-size: 16px;
			}
		}
	}
}

.search-area {
	--search-bg: var(--white-dark);
	--search-active-color: var(--decor-color);
	--search-input-color: var(--font-color);
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	max-width: 220px;
	width: 100%;
	height: 30px;
	background: var(--search-bg);
	border-radius: 4px;
	border: 1px solid var(--search-bg);
	&:focus-within {
		border-color: var(--search-active-color);
	}
	input.search-area__input {
		display: block;
		padding-right: 30px;
		width: 100%;
		height: 100%;
		font-style: 16px;
		font-weight: 700;
		color: var(--search-input-color);
		background: transparent;
		border: none;
		border-radius: inherit;
		&:focus {
			&::-webkit-input-placeholder {
				/* Chrome/Opera/Safari */
				opacity: 0.6;
			}
			&::-moz-placeholder {
				/* Firefox 19+ */
				opacity: 0.6;
			}
			&:-ms-input-placeholder {
				/* IE 10+ */
				opacity: 0.6;
			}
			&:-moz-placeholder {
				/* Firefox 18- */
				opacity: 0.6;
			}
		}
		&::-webkit-input-placeholder {
			/* Chrome/Opera/Safari */
			color: inherit;
		}
		&::-moz-placeholder {
			/* Firefox 19+ */
			color: inherit;
		}
		&:-ms-input-placeholder {
			/* IE 10+ */
			color: inherit;
		}
		&:-moz-placeholder {
			/* Firefox 18- */
			color: inherit;
		}
	}
	&__img {
		position: absolute;
		top: 50%;
		right: 6px;
		transform: translateY(-50%);
		display: block;
		width: 18px;
		height: 18px;
		z-index: 2;
	}
}

.registration {
	&__row {
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 10px;
	}
}
.lang {
	--lang-bg: var(--gray-dark);
	--lang-current-bg: var(--gray-dark);
	--lang-list-bg: var(--gray-dark);
	--lang-font-color: var(--gray);
	--lang-link-current-bg: var(--white);
	position: relative;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	&--active {
		.lang__list {
			pointer-events: all;
			opacity: 1;
			transform: translateY(3px);
		}
	}
	&__current {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 30px;
		height: 30px;
		background: var(--lang-current-bg);
		border-radius: 50%;
		cursor: pointer;
		.lang__img {
			width: 30px;
			height: 30px;
		}
	}
	&__list {
		position: absolute;
		top: 100%;
		left: 0;
		display: flex;
		flex-direction: column;
		padding: 5px;
		width: 180px;
		border-radius: 5px;
		background: var(--lang-list-bg);
		transform: translateY(25px);
		z-index: 3;
		pointer-events: none;
		opacity: 0;
		transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
	}
	&__item {
		order: 2;
		margin-bottom: 3px;
		&--current {
			order: 1;
		}
	}
	&__link {
		display: flex;
		align-items: center;
		width: 100%;
		height: 40px;
		font-size: 12px;
		color: var(--lang-font-color);
		text-transform: uppercase;
		border-radius: 4px;
		&--current {
			--lang-font-color: var(--white);
		}
		&:hover {
			--lang-font-color: var(--black);
			background: var(--lang-link-current-bg);
		}
	}
	&__img {
		display: block;
		width: 40px;
		height: 40px;
	}
	&__info {
		padding: 0 5px;
	}
	@media screen and (min-width: 1800px) {
		&__current {
			width: 40px;
			height: 40px;
			.lang__img {
				width: 40px;
				height: 40px;
			}
		}
	}
}
.login {
	&__row {
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 10px;
	}
}
.user {
	--user-color: var(--white-dark);
	--shadow-color: var(--bg-color);
	--user-decor-color: var(--white);
	--percent-bg: var(--gray-dark);
	--short-info-bg: var(--black-dark);
	--short-border-color: var(--gray-dark);
	position: relative;
	display: block;
	max-width: 340px;
	width: 100%;
	color: var(--user-color);
	&__header {
		display: flex;
		align-items: center;
	}
	&__short-info {
		// flex: 1;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		margin-left: auto;
		padding: 4px 15px 4px 5px;
		min-height: 32px;
		max-height: 42px;
		background: var(--short-info-bg);
		border-radius: 4px;
		border: 1px solid var(--short-border-color);
		white-space: nowrap;
	}
	&__value-side {
		// flex: 1;
		padding: 0 10px;
		font-weight: 700;
		text-align: right;
	}
	&__value-item {
		font-size: 14px;
		// &--bonus {
		// 	color: var(--decor-color);
		// 	font-size: 12px;
		// 	sup {
		// 		font-size: 0.4em;
		// 	}
		// }
		sup {
			font-size: 0.4em;
		}
	}
	&__type-side {
		position: relative;
		padding: 0 10px;
		font-size: 10px;
		line-height: 16px;
		color: var(--gray);
		text-transform: uppercase;
		&::after {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			transform: translateX(-50%);
			display: block;
			width: 1px;
			height: 100%;
			background: var(--decor-color);
		}
	}
	&__img-area {
		display: flex;
		align-items: center;
		margin-left: -10px;
	}
	&__img-box {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 32px;
		min-width: 32px;
		height: 32px;
		border-radius: 4px;
		border: 1px solid var(--short-border-color);
	}
	&__img {
		display: block;
		width: 30px;
		min-width: 30px;
		height: 30px;
		border-radius: 4px;
		object-fit: cover;
	}
	&__status-box {
		display: block;
		padding: 0 10px;
		max-width: 100px;
		width: 100%;
		text-align: right;
	}
	&__name {
		display: block;
		max-width: 100px;
		width: 100%;
		font-size: 12px;
		font-weight: 400;
		color: var(--user-decor-color);
		// text-transform: capitalize;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	&__status {
		display: block;
		max-width: 70px;
		width: 100%;
		font-size: 10px;
		color: var(--gray);
		text-transform: uppercase;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	&__progress {
		display: flex;
		margin: 4px -10px 0; 
		// align-items: center;
		// gap: 4px;
		// font-size: 10px;
		// color: var(--user-decor-color);
	}
	// &__progress-percent {
	// 	padding: 0 3px;
	// }
	&__progress-box {
		flex: 1;
		display: block;
		padding: 1px;
		height: 10px;
		background: var(--black-dark);
		border: 1px solid var(--gray-dark);
	}
	&__progress-line {
		display: block;
		height: 100%;
		width: attr(data-width);
		background: var(--decor-color);
	}
	&__alert {
		position: absolute;
		top: 0;
		right: 0;
		transform: translate(50%, -1px);
		display: flex;
		justify-content: center;
		align-items: center;
		width: 16px;
		height: 16px;
		font-size: 10px;
		line-height: 11px;
		font-weight: 700;
		background-color: var(--red);
		border-radius: 50%;
		z-index: 3;
	}
	@media screen and (min-width: 1800px) {
		&__short-info {
			min-height: 42px;
			height: 42px;
		}
		&__value-side {
			padding: 0 15px;
		}
		&__value-item {
			font-size: 18px;
		}
		&__img-box {
			width: 42px;
			min-width: 42px;
			height: 42px;
		}
		&__img {
			width: 40px;
			min-width: 40px;
			height: 40px;
		}
		&__alert {
			transform: translate(50%,-7px);
			width: 20px;
			height: 20px;
			font-size: 12px;
		}
	}
}
