.win-now {
	--min-width: 220px;
	--bg-color: var(--white-dark);
	display: block;
	overflow: hidden;
	background: var(--bg-color);
	&__row {
		display: flex;
		// gap: 10px;
		mask-image: linear-gradient(
			90deg,
			#fff,
			#fff 3%,
			#fff 97%,
			hsla(0deg, 0%, 100%, 0)
		);
	}
	&__item {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		padding: 10px;
		width: var(--min-width);
		min-width: var(--min-width);
		font-size: 10px;
		letter-spacing: 0.05em;
		color: var(--font-color);
		text-align: center;
	}
	&__value {
		--win-value-font-color: var(--white);
		--win-value-bg-color: var(--decor-color);
		position: relative;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		margin-top: -10px;
		padding: 0 7px;
		width: auto;
		min-width: 100px;
		height: 20px;
		font-size: 12px;
		font-weight: 700;
		color: var(--win-value-font-color);
		background: var(--win-value-bg-color);
		border-radius: 4px;
		z-index: 2;
	}
	&__player {
		display: flex;
		justify-content: center;
		align-items: center;
	}
	&__game-link {
		display: flex;
		justify-content: center;
		align-items: center;
		width: auto;
		color: inherit;
		text-transform: uppercase;
		span {
			display: inline-block;
			&::first-letter {
				text-transform: capitalize;
			}
		}
		&:hover {
			color: var(--decor-color);
		}
	}
	.game-item {
		z-index: 1;
		&__label,
		&__btn,
		&__title {
			display: none;
			visibility: hidden;
		}
	}
	@media screen and (min-width: 1800px) {
		&__item {
			font-size: 12px;
			line-height: 1.4;
		}
		&__value {
			padding: 0 15px;
			height: 30px;
			font-size: 14px;
		}
	}
}
