.betting-category {
	display: block;
	width: 100%;
	overflow-x: auto;
	&__list {
		display: flex;
		align-items: center;
		padding: 5px 15px;
	}
	&__item {
		display: inline-block;
		padding: 0 5px;
		&:first-child {
			padding-left: 0;
		}
	}
	&__link {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		width: auto;
		min-width: 30px;
		height: 30px;
		font-size: 12px;
		font-weight: 700;
		color: var(--font-color);
		border-radius: 4px;
		background: var(--gray-dark);
		overflow: hidden;
		&:hover,
		&--active {
			background: var(--white);
		}
		&--active {
			padding: 0 8px;
			.betting-category {
				&__link-info-box {
					width: auto;
				}
				&__link-info {
					transform: translateY(0);
				}
			}
		}
	}
	&__img {
		display: block;
		width: 24px;
		height: 24px;
	}
	&__link-info-box {
		display: block;
		width: 0;
	}
	&__link-info {
		display: inline-block;
		padding: 0 10px;
		white-space: nowrap;
		transform: translateY(200%);
		transition: transform 0.2s ease-in-out;
		&::first-letter {
			text-transform: capitalize;
		}
	}
	@media screen and (min-width: 1800px) {
		&__list {
			padding: 5px 50px;
		}
		&__link {
			min-width: 40px;
			height: 40px;
		}
	}
}
