.account {
	&__grid {
		display: block;
		// display: grid;
		// justify-items: center;
		// gap: 20px;
		margin: 0 -10px;
	}
	&__bonus-grid {
		display: grid;
		justify-content: center;
		justify-items: center;
		grid-template-columns: repeat(auto-fit, 320px);
		gap: 10px 0;
		padding: 0 5px 20px;
		width: 100%;
	}
	&__row {
		display: flex;
		justify-content: space-around;
		align-items: center;
		flex-wrap: wrap;
		gap: 10px;
	}
	&__body {
		display: block;
		margin-bottom: 20px;
		padding: 20px;
		width: 100%;
		color: var(--font-color);
		background: var(--white-dark);
		&--active {
			box-shadow: inset 0px 0px 0px 10px var(--yellow);
		}
		&--transparent {
			background: transparent;
		}
	}
	&__shape {
		display: block;
		max-width: 420px;
		width: 100%;
		color: var(--font-color);
		&--medium {
			max-width: 860px;
		}
		&--large {
			max-width: 1260px;
		}
	}
	&__shape {
		margin: 0 auto;
	}
	&__messaage {
		position: relative;
		display: block;
		margin-bottom: 20px;
		padding: 10px 10px 20px;
		line-height: 1.6667;
		background: var(--white-dark);
		&--new {
			background: var(--white);
			.account__messaage-date {
				color: var(--red);
			}
		}
		a {
			color: var(--decor-color);
			text-decoration: underline;
			&:hover {
				text-decoration: none;
			}
		}
	}
	&__messaage-close {
		position: absolute;
		top: -5px;
		right: -5px;
		z-index: 2;
	}
	&__messaage-date {
		display: block;
		margin-bottom: 5px;
		padding-right: 20px;
		color: var(--gray);
	}
	&__img-box {
		position: relative;
		display: flex;
		justify-content: center;
		&--active {
			.account__img-gallery {
				transform: translate(-50%, 0);
				opacity: 1;
				pointer-events: all;
			}
		}
	}
	&__img-gallery-body {
		position: relative;
		display: inline-block;
		padding: 25px 0 15px;
		background-color: var(--gray-dark);
		border-radius: 4px;
	}
	&__img-current,
	&__img-item {
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 4px;
		overflow: hidden;
		cursor: pointer;
	}
	&__img-current {
		--bg-color: var(--gray-dark);
		position: relative;
		padding-right: 20px;
		width: 91px;
		min-width: 91px;
		height: 72px;
		background: var(--bg-color);
		border: 1px solid var(--bg-color);
		&:hover {
			--bg-color: var(--decor-color);
		}
		&:before {
			content: "";
			display: block;
			position: absolute;
			top: 45%;
			right: 4px;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 5px 5px 0 5px;
			border-color: var(--white) transparent transparent transparent;
			transition: transform 0.3s ease-in-out;
			z-index: 1;
		}
	}
	&__img-item {
		--border-color: var(--white);
		width: 70px;
		min-width: 70px;
		height: 70px;
		&--current,
		&:hover {
			border: 2px solid var(--border-color);
		}
	}
	&__img {
		display: block;
		width: 70px;
		min-width: 70px;
		height: 70px;
		border-radius: 4px;
		object-fit: cover;
	}
	&__img-gallery {
		position: absolute;
		top: -5px;
		left: 50%;
		transform: translate(-50%, 20%);
		box-shadow: 0 4px 10px 0 rgb(21 21 21 / 10%);
		// border-radius: 25px;
		z-index: 2;
		pointer-events: none;
		opacity: 0;
		transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
	}
	&__img-list {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		gap: 10px;
		min-width: 180px;
		max-height: 310px;
		overflow-x: hidden;
		overflow-y: auto;
	}
	&__img-gallery-close {
		position: absolute;
		top: -10px;
		right: -10px;
	}
	&__favorite-game {
		position: relative;
		display: block;
		max-width: 300px;
		width: 100%;
	}
	&__favorite-remove {
		position: absolute;
		top: -5px;
		right: -4px;
		z-index: 5;
	}
	&__filter-box {
		.input-area {
			margin-top: 0;
			max-width: 180px;
			&::after {
				content: none;
			}
		}
		.btn {
			height: 35px;
		}
	}
	&__filter-row {
		display: flex;
		justify-content: space-between;
		align-items: center;
		flex-wrap: wrap;
		gap: 10px;
		padding-bottom: 10px;
		width: 100%;
	}
	&__filter-item {
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 10px;
		width: 100%;
	}
	&__form-row {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-bottom: 20px;
		.btn {
			margin-top: 26px;
		}
	}
	&__form-grid {
		display: grid;
		// justify-items: center;
		// justify-content: center;
		& > * {
			order: 9;
		}
	}
	@media screen and (min-width: 600px) {
		&__grid {
			margin: 0 auto;
		}
		&__body {
			padding: 20px 40px;
		}
	}
	@media screen and (min-width: 760px) {
		&__filter-item {
			width: auto;
			.select--big {
				min-width: 180px;
			}
		}
		&__form-row {
			display: flex;
			flex-direction: row;
			justify-content: center;
			align-items: flex-end;
			gap: 10px;
			.btn {
				margin-top: 26px;
				height: 35px;
			}
		}
	}
	@media screen and (min-width: 1100px) {
		&__form-grid {
			display: grid;
			grid-template-columns: minmax(300px, 400px) minmax(300px, 400px) auto;
			// justify-items: center;
			justify-content: center;
			gap: 0 20px;
			& > * {
				order: 9;
			}
			&-order-1 {
				order: 1;
				grid-column-start: 1;
				grid-column-end: -1;
			}
			&-order-2 {
				order: 2;
			}
			&-order-3 {
				order: 3;
			}
			&-order-4 {
				order: 4;
			}
			&-order-5 {
				order: 5;
			}
			&-order-6 {
				order: 6;
			}
			.btn-row {
				padding: 26px 0 0;
			}
			.btn {
				height: 35px;
			}
			.table {
				margin-top: 15px;
			}
		}
		&__filter-row {
			justify-content: flex-start;
		}
		&__filter-item {
			&:last-child {
				margin-left: auto;
			}
		}
		&__messaage {
			padding: 20px 40px 30px;
		}
		&__messaage-date {
			padding-right: 0;
		}
	}
	// @media screen and (min-width: 1600px) {
	// &__form-item {
	// 	flex: 1;
	// 	max-width: 380px;
	// }
	// }
	@media screen and (min-width: 1800px) {
		&__body {
			padding: 35px 40px;
		}
		&__bonus-grid {
			grid-template-columns: repeat(auto-fit, 480px);
		}
		&__shape {
			&--large {
				max-width: 1260px;
			}
		}
		&__form-row {
			gap: 20px;
		}
		.btn {
			height: 50px;
		}
	}
}
