.betting-event {
	&__slider {
		display: none;
		visibility: hidden;
		margin: 0 -5px;
		&.slick-initialized {
			display: block;
			visibility: visible;
		}
		.slick-list {
			mask-image: linear-gradient(
				90deg,
				#fff,
				#fff 2%,
				#fff 97%,
				hsla(0deg, 0%, 100%, 0)
			);
			// padding-right: calc(100vw - 280px);
		}
	}
	&__item {
		display: block;
		padding: 0 5px;
		width: 260px;
		// height: 200px;
		color: var(--font-color);
		text-align: center;
		overflow: hidden;
	}
	&__info-area {
		background: var(--white-dark);
	}
	&__title {
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 0 10px;
		width: 100%;
		height: 25px;
	}
	&__row {
		display: grid;
		grid-template-columns: 38% 24% 38%;
	}
	&__player {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	&__player-img-box {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 60px;
		height: 60px;
		background: var(--white);
		border-radius: 4px;
	}
	&__player-img {
		display: block;
		max-width: 40px;
		max-height: 50px;
		height: auto;
	}
	&__player-name {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 15px;
		font-size: 10px;
		text-transform: uppercase;
	}
	&__game-info {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		height: 80px;
	}
	// &__game-img {
	// 	display: block;
	// 	width: 48px;
	// 	height: 44px;
	// }
	&__game-time {
		font-size: 14px;
		font-weight: 700;
	}
	&__game-date {
		font-size: 10px;
	}
	&__link {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 30px;
		font-size: 10px;
		line-height: 11px;
		color: var(--white-dark);
		text-transform: uppercase;
		background: var(--gray-dark);
		&:hover {
			// color: var(--font-color);
			background: var(--decor-color);
			.betting-event__link-frame {
				color: var(--white-dark);
				background: var(--decor-color);
			}
		}
		b {
			font-size: 14px;
		}
	}
	&__link-frame {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 20px;
		color: var(--font-color);
		background: var(--white-dark);
		border-radius: 2px;
	}
	@media screen and (min-width: 1820px) {
		&__title {
			height: 40px;
		}
		&__item {
			width: 316px;
			padding: 0 8px;
		}
		&__player-img-box {
			width: 80px;
			height: 80px;
		}
		&__player-img {
			max-width: 60px;
			max-height: 70px;
		}
		&__player-name {
			height: 30px;
			font-size: 14px;
		}
		&__game-time {
			font-size: 16px;
		}
		&__game-date {
			font-size: 12px;
		}
		&__link {
			height: 50px;
			font-size: 12px;
			line-height: 14px;
			b {
				font-size: 16px;
			}
		}
	}
	@media screen and (min-width: 1920px) {
		&__slider {
			.slick-list {
				mask-image: none;
			}
		}
	}
}
