.popup {
	--popup-bg: var(--white-dark);
	position: fixed;
	top: 20px;
	left: 50%;
	display: inline-block;
	padding: 10px;
	width: auto;
	z-index: 9;
	transform: translate(-50%, -40px);
	opacity: 0;
	pointer-events: none;
	transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
	&--active {
		transform: translate(-50%, 0);
		opacity: 1;
		pointer-events: all;
	}
	&__body {
		position: relative;
		display: inline-flex;
		padding: 31px 0;
		width: auto;
		height: auto;
		background: var(--popup-bg);
	}
	&__close {
		position: absolute;
		top: 1px;
		right: 1px;
		z-index: 2;
	}
	&__content {
		padding: 0 20px;
		max-width: 100vw;
		min-width: 300px;
		max-height: calc(100vh - 140px);
		height: auto;
		font-size: 14px;
		// line-height: 16px;
		overflow-x: hidden;
		overflow-y: auto;
		&--no-padding {
			padding: 0;
		}
		&::-webkit-scrollbar-track {
			background: var(--white-dark);
		}
		a:not(.bonus-info__link-close) {
			color: var(--decor-color);
			text-decoration: underline;
			&:hover {
				text-decoration: none;
			}
		}
	}
	.heading {
		--heading-bg: var(--white-dark);
	}
	@media screen and (min-width: 440px) {
		&__content {
			padding: 0 40px;
			min-width: 420px;
			&--no-padding {
				padding: 0;
			}
		}
	}
}

.registration-popup {
	display: block;
	max-width: 860px;
	&__deposit-box {
		display: block;
		max-width: 430px;
		width: 100vw;
		.tab__menu-btn {
			--btn-bg-color: var(--gray);
		}
	}
	&__info-box {
		padding: 0 20px 10px;
	}
	&__item {
		display: flex;
		justify-content: center;
		padding: 0 20px;
		max-width: 430px;
		width: 100vw;
		&--no-padding {
			padding: 0;
		}
	}
	&__alert-box {
		position: relative;
		display: block;
		padding-top: 70px;
		width: 100%;
	}
	&__alert-img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: top;
		z-index: 1;
	}
	&__alert-info {
		position: relative;
		display: block;
		padding: 10px 30px;
		color: var(--white);
		text-align: center;
		background-color: rgba(23, 26, 30, 0.7);
		z-index: 2;
		& > * {
			margin-bottom: 0.5em;
		}
	}
	&__form-grid {
		padding: 10px 20px;
	}
	@media screen and (min-width: 360px) {
		&__info-box {
			padding: 0 40px 10px;
		}
		&__item {
			padding: 0 40px;
			&--no-padding {
				padding: 0;
			}
		}
		&__form-grid {
			padding: 10px 40px;
		}
	}
	@media screen and (min-width: 760px) {
		&__deposit-box {
			max-width: 490px;
			.form-container {
				margin: 0 auto;
			}
		}
	}
	@media screen and (min-width: 900px) {
		&__row {
			display: flex;
			justify-content: center;
			padding: 0 20px;
		}
		&__item {
			padding: 0 20px;
			width: 410px;
			&--no-padding {
				padding: 0;
			}
		}
		&__alert-box {
			display: flex;
			align-items: flex-end;
		}
		&__deposit-box {
			max-width: 860px;
			.tab__menu--payment {
				justify-content: center;
			}
			.btn-row {
				padding: 26px 0 0;
			}
			.btn {
				height: 35px;
			}
			.table {
				margin-top: 15px;
			}
		}
		&__form-grid {
			display: grid;
			grid-template-columns: minmax(300px, 400px) minmax(300px, 400px) auto;
			justify-content: center;
			gap: 0 10px;
			padding-top: 20px;
			& > * {
				order: 9;
			}
		}
		&__form-grid-order-1 {
			order: 1;
			grid-column-start: 1;
			grid-column-end: -1;
		}
		&__form-grid-order-2 {
			order: 2;
		}
		&__form-grid-order-3 {
			order: 3;
		}
		&__form-grid-order-4 {
			order: 4;
		}
		&__form-grid-order-5 {
			order: 5;
		}
		&__form-grid-order-6 {
			order: 6;
		}
	}
	@media screen and (min-width: 1800px) {
		max-width: 1260px;
		width: 1260px;
		&__deposit-box {
			max-width: 1260px;
			.btn {
				height: 50px;
			}
		}
		&__form-grid {
			grid-template-columns: 400px 400px auto;
			gap: 0 40px;
		}
	}
}
